body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 1100px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.content{
    display: -webkit-flex;
    display: flex;
}


.error{
    text-align: center;
    margin-top:16vw;
    color: red;
}

.head-section{
    display: -webkit-flex;
    display: flex;
    margin :0  auto;
    background-color:  rgb(36, 36, 36);
    -webkit-align-items: center;
            align-items: center;
    height: auto;
    padding: 10px;
    width:  auto;
    font-family:  'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.logo{
    min-width: auto;
}

.logo>a{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: white;
    text-decoration: none;
    padding-right: 15px ;
}


img{
    margin: 10px;
    height: 25px;
    margin-left: 1vw;
}

input{
    width: 25vw;
    margin-left:  5vw;
    font-size:  14px;
    padding: 12.5px;
    height: 10px;
    background-color: rgb(116, 116, 116);
    color: white;
    border:  0.005px solid silver;
    border-radius: 2.5px;
    font-weight: bold;
    letter-spacing: 1px;
}

.logo>a>span{
    font-weight: 600;
    font-size: 20px;
}

.logo>a:hover{
    color: rgb(108, 204, 108);
}

input:focus{
    background-color:  white;
    color: black;
    width: 35vw;
    transition:  all .3s;
}

::-webkit-input-placeholder{
color: rgb(161, 154, 154);
font-weight: 100;
}

::-ms-input-placeholder{
color: rgb(161, 154, 154);
font-weight: 100;
}

::placeholder{
color: rgb(161, 154, 154);
font-weight: 100;
}


.content{
    display: -webkit-flex;
    display: flex;
}

.left-section{
          margin: 2.5vw 3.5vw 0 8.5vw ;
          width:  18.5vw;
          min-width:  220px;
}

.tile{
      text-decoration:  none;
      display: inline-block;
      outline:  1px ridge silver;
}


.tile>img{
    min-height: 220px;
    min-width:  220px;
    height: 17.5vw;
    width: 18.5vw;
    border: none;
    margin : 0;
}

.name{
    text-align: center;
    margin:  1vw 0 0.75vw 0;
    color:  black;
    font-weight: bolder;
    font-size: 20px;
}



.uname{
    margin: 1.5vw 1vw 0.5vw 0;
    width: auto;
    text-align: center;
    min-width:  220px;
}


.uname{
    font-weight: bold;
    text-decoration: none;
    color: rgb(34, 34, 34);
    font-size:  22px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}


.bio{
    text-align: center;
    color: rgb(46, 39, 39);
    min-width:  180px;
    margin-top: 0.2vw;
    font-size:  15px;
}

#follow{
    margin: 1vw 0 1.45vw 0;
    width: 100%;
    height: 37.5px;
    font-size: 13px;
    font-weight: bolder;
    background-color:  rgb(240, 240, 240);
    border:  1px solid silver;
    color: rgb(19, 19, 19);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    border-radius: 2.5%;
    min-width:  220px;
}

#follow:hover{
    background-color:  #E6EBF1;
    border:  1px solid rgb(158, 158, 158);
}


.right-section{
    margin: 1vw 1.5vw  0 2.5vw;
}


.menu{
    padding: 15px 0 16.5px 0 ;
    border-bottom:  rgb(207, 207, 207) solid 0.7px;
}


.menu>li{
    display: inline-block;
    margin-right:  1.5vw;
}

.tab{
    text-decoration: none;
    color: rgb(90, 90, 90);
    padding: 15px;
    font-size: 15px;
}

.val{
    margin-left: 0.5vw;
    background-color: rgb(224, 224, 224);
    padding: 3px 5px 3px 5px;
    border-radius: 40%;
    font-size: 12px;
    font-weight: bold;
}


.tab:hover{
    color: rgb(32, 32, 32);
    border-bottom: rgb(219, 219, 219) solid  2px;
}

.tab:focus{
    color: black;
    font-weight: bolder;
    border-bottom: rgb(255, 102, 0) solid 2px;
}


.repo-info{
    display: grid;
    grid-template-columns: auto auto;
}



.company{
    text-align: left;
    margin: 1.5vw 0 0 0.35vw;
    font-size: 13px;
    line-height: 21px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
color: black;
}


.loc{
    text-align: left;
    margin-top: 0.75vw;
    font-size: 13px;
    font-weight: 600;
    line-height: 21px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
color: black;
}

.loc>img{
    vertical-align: middle;
    margin:  0  0 3px 0 ;
    height:  22.5px;
}

.blog{
    text-align: left;
    margin: 0.5vw 0 0 0.6vw;
    line-height: 21px;
}

.blog>a{
    color:  #0366D6;
    text-decoration: none;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size:  12px;
    font-weight: bold;
}

.blog>a:hover{
    text-decoration:  underline;
}

.item{
  background-color: rgba(255, 255, 255, 0.8);
  border: 1px solid #d1d5da;
  font-size: 14px;
  margin: 0  16px 16px 0;
  padding: 16px;
  width: 350px;
  height: 100px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif sans-serif -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  border-radius: 10px;
}


.title{
    margin-bottom: 12.5px;
}

.title>a{
    text-decoration: none;
    font-weight: 600;
    font-size: 14px;
    color: #0366D6;
    line-height: 21px;
}

.extra-info{
    font-size: 12px;
    line-height: 18px;
    margin: 8px 0 16px 0;
    color: #586069;
    height: 36px;
    overflow: hidden;
}


.add-info{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: #586069;
    line-height:  12px;
}


.list{
    margin-right: 7.5px;
    display: inline-block;
    margin-right: 30px;
    font-size: 12.5px;
}

.list>img{
    height: 12px;
    vertical-align: middle;
    margin: 0 7.5px 3.5px 0;
}


.add-info:hover{
    color: #0366D6;
}

.title:hover{
    text-decoration: underline;
}
